import React, { Component } from 'react';

import {Api} from '../../../Api'
import { Email, Telefone } from '../../../Componentes'
import { AlunoCell } from '../../../Componentes/AlunoCell';
import { formatDatePtBr } from '../../../utils';

export default class EmitirTCE extends Component {
    api = new Api()

    constructor(){
        super()
        this.state = {
            listaDeTCE : [],
            loadingIds: [],
        }
    }

    componentDidMount(){
        this.api.TermosDeCompromisso
            .ListaTermosDeCompromisso()
            .then((listaDeTCE) => this.setState({listaDeTCE}))
            .catch((erro) => console.log("erro", erro));
    }

    linkParaBaixarTermo(registro = ''){
        const {api} = this.props
        return `${api.TermosDeCompromisso.getBaseUrl()}/${registro}/baixar`
    }

    isLoading(id){
        return this.state.loadingIds.includes(id);
    }

    setLoading(id){
        this.setState(state => state.loadingIds.includes(id) ? state : ({
            ...state,
            loadingIds: [...state.loadingIds, id]
        }))
    }

    setLoaded(id){
        this.setState(state => !state.loadingIds.includes(id) ? state : ({
            ...state,
            loadingIds: state.loadingIds.filter(v => v !== id)
        }))
    }

    async baixar(registro){
        const {api} = this.props
        const token = api.Auth.accessToken()
        const link = this.linkParaBaixarTermo(registro)

        this.setLoading(registro);
        await fetch(link, {
            headers : {
                "Authorization" : `Bearer ${token}`
            }
        })
        .then(res => res.arrayBuffer())
        .then(buffer => {
            const blob = new Blob([buffer], { type: "application/pdf" });
            const blobUrl = URL.createObjectURL(blob);

            this.setState(prevState => {
                const {listaDeTCE} =  prevState
                
                const termo = listaDeTCE.filter(termo => termo.registro === registro)[0]
                const termoAtualizado = {...termo, blobUrl}

                const listaDeTCEAtualizada = listaDeTCE.reduce( (listaNova, valorOriginal) => {
                    listaNova.push( valorOriginal.registro === registro ? termoAtualizado : valorOriginal )
                    return listaNova
                }, [])

                return ({
                    ...prevState,
                    listaDeTCE : [...listaDeTCEAtualizada]
                })
            })
        });
        this.setLoaded(registro);
    }

    render() {
        const {listaDeTCE} = this.state

        return (
            <div>
                <h5>Listar TCE</h5>
                <table className="table table-striped table-hover">
                    <thead>
                        <tr>
                            <th></th>
                            <th>Estagi√°rio(a)</th>
                            <th>Contrato</th>
                            <th>Orienta√ß√£o/Supervis√£o</th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                    {
                        listaDeTCE.map((termo, i) => {
                            const { vigencia, aluno, local_do_estagio, orientador, supervisor } = termo

                            return (
                                <tr key={termo.id}>
                                    <td>{i+1}</td>
                                    <td>
                                        <AlunoCell aluno={aluno} />
                                    </td>
                                    <td>
                                        <div><small>N.: {termo.registro }</small></div>
                                        <div><small>Vig√™ncia: { `${formatDatePtBr(new Date(vigencia.inicio))} a ${formatDatePtBr(new Date(vigencia.fim))}` }</small></div>
                                        <div><small>Local: {local_do_estagio.nome}</small></div>
                                    </td>
                                    <td>
                                        <div><small>Orientador: {orientador.nome}</small></div>
                                        <div className="border-bottom"/>
                                        <div><small>Supervisor: {supervisor.nome}</small></div>
                                        <div><small><Telefone value={supervisor.telefone}/> {supervisor.email && <> | <Email value={supervisor.email}/></>}</small></div>
                                    </td>
                                    <td>
                                        { termo.blobUrl === undefined
                                            ? this.isLoading(termo.registro)
                                                ? "Baixando..."
                                                : <button onClick={() => this.baixar(termo.registro)}>Baixar</button>
                                            :
                                                <button onClick={() => window.open(termo.blobUrl, '_blank')}>Abrir</button>
                                        }
                                        <button onClick={() => this.aditivarTermo(termo)}>Aditivar</button>
                                    </td>
                                </tr>
                            )
                        })
                    }
                    </tbody>
                </table>
            </div>
        )
    }
}
